import { apolloClientInstance } from '@/vue-apollo'
import Login from '@/graphql/auth/Login.graphql'
import Logout from '@/graphql/auth/Logout.graphql'
import Register from '@/graphql/auth/Register.graphql'
import UserUpdateRegistrationType from '@/graphql/auth/UserUpdateRegistrationType.graphql'

export function register(query, callback) {
  console.log('query', query)
  apolloClientInstance.mutate({
    mutation: Register,
    variables: query
  }).then(function(response) {
    console.log('response.data', response.data)
    callback(response.data, true)
  }).catch(function(response) {
    console.log('response', response)
    callback(response, false)
  })
}

export function userUpdateRegistrationType(query, callback) {
  apolloClientInstance.query({
    query: UserUpdateRegistrationType,
    variables: query
  }).then(function(response) {
    callback(response.data, true)
  }).catch(function(response) {
    callback(response, false)
  })
}

export function login(query, callback) {
  apolloClientInstance.mutate({
    mutation: Login,
    variables: query
  }).then(function(response) {
    callback(response.data, true)
  }).catch(function(response) {
    callback(response, false)
  })
}

export function logout(callback) {
  apolloClientInstance.mutate({
    mutation: Logout
  }).then(function(response) {
    callback(response.data, true)
  }).catch(function(response) {
    callback(response, false)
  })
}
