<template>
  <div class="dashboard-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <h3>{{ this.$route.name }}</h3>
        <el-dropdown style="margin-left: 1%; float: right;" @command="exportData">
          <el-button
            style="width: 100%"
            size="medium"
            class="filter-item actionSizes"
            icon="el-icon-download"
            :loading="printLoading"
          >
            Export as<i class="el-icon-arrow-down el-icon--right" />
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="xlsx">Excel</el-dropdown-item>
            <el-dropdown-item command="pdf">PDF</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <!--        <el-button-->
        <!--          class="actionSizes"-->
        <!--          style="margin-left: 1%; float: right;"-->
        <!--          size="medium"-->
        <!--          icon="el-icon-plus"-->
        <!--          @click="addNewRecord"-->
        <!--        >-->
        <!--          Add New Record-->
        <!--        </el-button>-->
        <div style="float: right;" class="actionSearchSizes">
          <el-input v-model="searchValue" size="medium" placeholder="search here" class="input-with-select" @keyup.enter.native="handleSearch">
            <el-button
              slot="append"
              style="background-color: #698bd0; color: white"
              icon="el-icon-search"
              class="btn-search"
              @click="handleSearch"
            >Search
            </el-button>
          </el-input>
        </div>
      </div>
      <pagination
        v-show="total>0"
        :total="total"
        :page.sync="query.page"
        :limit.sync="query.first"
        @pagination="get"
      />
      <el-table
        v-loading="tableLoading"
        :header-cell-style="{ background: '#7c96a6', color: 'white' }"
        style="width: 100%"
        stripe
        border
        fit
        :data="userLogs"
        height="650"
      >
        <el-table-column
          prop="id"
          type="index"
          label="#"
          :index="indexMethod"
          width="50"
          align="center"
        />
        <el-table-column
          label="Username"
          width="auto"
        >
          <template slot-scope="{row}">
            {{ row.user.email }}
          </template>
        </el-table-column>
        <el-table-column
          label="Date Log-In"
          width="auto"
        >
          <template slot-scope="{row}">
            {{ row.date_in }}
          </template>
        </el-table-column>
        <el-table-column
          label="Time-In"
          width="auto"
        >
          <template slot-scope="{row}">
            {{ row.time_in }}
          </template>
        </el-table-column>
        <el-table-column
          label="Date Log-Out"
          width="auto"
        >
          <template slot-scope="{row}">
            {{ row.date_out }}
          </template>
        </el-table-column>
        <el-table-column
          label="Time-Out"
          width="auto"
        >
          <template slot-scope="{row}">
            {{ row.time_out }}
          </template>
        </el-table-column>
        <el-table-column
          label="User Type"
          width="auto"
        >
          <template slot-scope="{row}">
            {{ row.user.registration_type }}
          </template>
        </el-table-column>
      </el-table>
      <div style="padding: 14px;">
        <pagination
          v-show="total>0"
          :total="total"
          :page.sync="query.page"
          :limit.sync="query.first"
          @pagination="get"
        />
      </div>
    </el-card>
    <call-out-form
      :form-visible="formVisible"
      :form-types="formTypes"
      :form-type="formType"
      :form-data="formData"
      @formSuccess="get"
    />
  </div>
</template>

<script>
import Pagination from '@/components/Pagination'
import CallOutForm from '@/views/guidance/dashboard/Form'
import {
  deleteUser
} from '@/graphql/users/'
import {
  registrationInfo
} from '@/graphql/fishery'
export default {
  name: 'Dashboard',
  components: {
    Pagination,
    CallOutForm
  },
  data() {
    return {
      formTypes: {
        add: 'add',
        edit: 'edit'
      },
      formType: '',
      formData: {},
      formVisible: false,
      tableLoading: false,
      printLoading: false,
      searchValue: '',
      currentUser: JSON.parse(localStorage.getItem('user')),
      query: {
        first: 10,
        page: 1,
        search: '',
        user: '',
        user_type: '',
        registration_type: ''
      },
      total: 0,
      userLogs: []
    }
  },
  watch: {
    searchValue: function() {
      if (this.searchValue === '') {
        this.handleSearch()
      }
    }
  },
  mounted() {
    this.get()
  },
  methods: {
    get() {
      this.formVisible = false
      this.tableLoading = true
      this.userLogs = []
      this.query.user = this.currentUser.id.toString()
      this.query.user_type = this.currentUser.registration_type.toString()
      this.query.registration_type = this.$route.name.toString()
      registrationInfo(this.query, (response, success) => {
        if (success) {
          const data = response.registrationInfo.data
          console.log('data', data)
          if (data.length > 0) {
            this.userLogs = data
            this.total = response.registrationInfo.paginator_info.total
            this.tableLoading = false
          } else {
            this.tableLoading = false
          }
        } else {
          this.tableLoading = false
          this.$message({
            message: `Unable to retrieve the data.`,
            dangerouslyUseHTMLString: true,
            type: 'error'
          })
        }
      })
    },
    handleSearch() {
      this.query.page = 1
      this.query.search = this.searchValue.toString()
      this.query.user = this.currentUser.id.toString()
      this.query.user_type = this.currentUser.registration_type.toString()
      this.query.registration_type = this.$route.name.toString()
      this.get()
    },
    indexMethod(index) {
      return (index + 1) + ((this.query.page - 1) * this.query.first)
    },
    openDeleteQuestion(data) {
      this.$confirm('This will permanently delete the record. Continue?', 'Warning', {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
        type: 'warning',
        center: true
      }).then(() => {
        deleteUser({ id: data.id }, (response, success) => {
          if (success) {
            this.$message({
              type: 'success',
              message: response.deleteUser.message
            })
            this.get()
          } else {
            this.$message({
              message: `Unable to retrieve the data.`,
              dangerouslyUseHTMLString: true,
              type: 'error'
            })
          }
        })
      })
    },
    addNewRecord() {
      // this.formVisible = true
      this.formType = this.formTypes.add
      this.formData = {}
    },
    updateRecord(row) {
      this.formVisible = true
      this.formType = this.formTypes.edit
      this.formData = row
    },
    exportData(type) {}
  }
}
</script>

