<template>
  <div class="login-container">
    <div v-if="client === 'guidance'" style="z-index: 1000; width: 100%; background-color: #f78180; padding: 1%; text-align: center; position: fixed">
      GUIDANCE OFFICE CONSULTATION SYSTEM
    </div>
    <el-form
      ref="refRegisterForm"
      :model="registerForm"
      :rules="registrationRules"
      class="login-form"
      auto-complete="on"
      label-position="left"
    >
      <el-card class="box-card" style="background-color: #ff9897">
        <div class="title-container">
          <div class="logo" style="text-align: center">
            <img width="60%" src="@/assets/guidance/logo.png">
            <h1>REGISTER</h1>
          </div>
        </div>
        <br>
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane name="student" label="Student" />
          <!--          <el-tab-pane name="teacher" label="Teacher" />-->
          <el-tab-pane name="guidance" label="Guidance" />
        </el-tabs>
        <el-form-item prop="name" style="background-color: #FFFFFF">
          <span class="svg-container">
            <svg-icon icon-class="user" />
          </span>
          <el-input
            ref="name"
            v-model="registerForm.name"
            placeholder="Enter full name"
            name="name"
            type="text"
            tabindex="1"
            auto-complete="off"
          />
        </el-form-item>

        <el-form-item prop="email" style="background-color: #FFFFFF">
          <span class="svg-container">
            <svg-icon icon-class="user" />
          </span>
          <el-input
            ref="email"
            v-model="registerForm.email"
            placeholder="Enter email"
            name="email"
            type="text"
            tabindex="2"
            auto-complete="off"
          />
        </el-form-item>

        <el-form-item prop="password" style="background-color: #FFFFFF">
          <span class="svg-container">
            <svg-icon icon-class="password" />
          </span>
          <el-input
            :key="passwordType"
            ref="password"
            v-model="registerForm.password"
            :type="passwordType"
            placeholder="Password"
            name="password"
            tabindex="2"
            auto-complete="on"
          />
          <span class="show-pwd" @click="showPwd">
            <svg-icon :icon-class="passwordType === 'password' ? 'eye' : 'eye-open'" />
          </span>
        </el-form-item>

        <el-form-item prop="password_confirmation" style="background-color: #FFFFFF">
          <span class="svg-container">
            <svg-icon icon-class="password" />
          </span>
          <el-input
            :key="passwordType"
            ref="password"
            v-model="registerForm.password_confirmation"
            :type="passwordType"
            placeholder="Confirm password"
            name="password_confirmation"
            tabindex="3"
            auto-complete="off"
            @keyup.enter.native="handleRegister"
          />
          <span class="show-pwd" @click="showPwd">
            <svg-icon :icon-class="passwordType === 'password' ? 'eye' : 'eye-open'" />
          </span>
        </el-form-item>
        <el-button
          :loading="loading"
          type="primary"
          style="width:100%;margin-bottom:30px;"
          @click.native.prevent="handleRegister"
        >SUBMIT</el-button>
        <span style="font-size: 16px">
          <router-link :to="{ path: '/'}" style="float: right"> Back to login </router-link>
        </span>
        <div style="margin-top: 20%; font-size: 14px">
          <div>Republic of the Philippines</div>
          <div style="font-weight: bold">OCCIDENTAL MINDORO STATE COLLEGE</div>
          <div>Sablayan, Occidental Mindoro</div>
          <div>Website: www.omsc.edu.ph</div>
          <div>Email address: omsc_9747@yahoo.com</div>
          <div>Tele/Fax: (043) 457-0231</div>
        </div>
      </el-card>
    </el-form>
  </div>
</template>

<script>
import {
  register,
  userUpdateRegistrationType
} from '@/graphql/auth'

export default {
  name: 'Register',
  data() {
    const client = process.env.VUE_APP_CLIENT
    return {
      client: client,
      registerForm: {
        name: '',
        email: '',
        password: '',
        password_confirmation: ''
      },
      registrationRules: {
        name: [{ required: true, trigger: 'blur' }],
        email: [{ required: true, trigger: 'blur' }],
        password: [{ required: true, trigger: 'blur' }],
        password_confirmation: [{ required: true, trigger: 'blur' }]
      },
      loading: false,
      passwordType: 'password',
      redirect: undefined,
      activeName: 'student',
      registration_type: 'Student'
    }
  },
  watch: {
    $route: {
      handler: function(route) {
        this.redirect = route.query && route.query.redirect
      },
      immediate: true
    }
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab.label)
      this.registration_type = tab.label
    },
    showPwd() {
      if (this.passwordType === 'password') {
        this.passwordType = ''
      } else {
        this.passwordType = 'password'
      }
      this.$nextTick(() => {
        this.$refs.password.focus()
      })
    },
    handleRegister() {
      this.$refs.refRegisterForm.validate(valid => {
        if (valid) {
          this.loading = true
          register(this.registerForm, (response, success) => {
            const res = response
            if (success) {
              const data = {
                id: parseInt(res.register.tokens.user.id),
                registration_type: this.registration_type,
                client: this.client
              }
              userUpdateRegistrationType(data, (response, success) => {
                if (success) {
                  const userData = response.userUpdateRegistrationType.data
                  if (userData) {
                    localStorage.setItem('access_token', res.register.tokens.access_token)
                    localStorage.setItem('user', JSON.stringify(userData))
                    this.$router.push({ path: '/' })
                  }
                } else {
                  this.$message({
                    message: response,
                    dangerouslyUseHTMLString: true,
                    type: 'error'
                  })
                }
              })
            } else {
              this.$message({
                message: response,
                dangerouslyUseHTMLString: true,
                type: 'error'
              })
            }
          })
          this.loading = false
        } else {
          this.loading = false
          return false
        }
      })
    }
  }
}
</script>

