<template>
  <el-dialog
    style="width: inherit"
    :title="textMap[formType]"
    :visible="formVisible"
    :before-close="exitForm"
    :destroy-on-close="true"
    :close-on-click-modal="false"
  >
    <el-form
      ref="dataForm"
      v-loading="loading"
      :rules="rules"
      :model="appointment"
      label-position="top"
    >
      <el-form-item label="Category" prop="category_id">
        <el-select v-model="appointment.category_id" style="width: 100%;" filterable>
          <el-option
            v-for="item in categoryOptions"
            :key="item.id"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="Notes" prop="student_notes">
        <el-input v-model="appointment.student_notes" type="textarea" :rows="5" />
      </el-form-item>
      <el-form-item label="College" prop="college">
        <el-input v-model="appointment.college" disabled />
      </el-form-item>
      <el-form-item label="Year and Section" prop="yrSection">
        <el-input v-model="appointment.yrSection" disabled />
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button class="cancel" size="medium" @click="onClose">CANCEL</el-button>
      <el-button class="button-primary" size="medium" @click="onSubmit">SAVE</el-button>
    </span>
  </el-dialog>

</template>

<script>
import {
  categoryInfos
} from '@/graphql/categories'
import {
  createUpdateStudentGuidanceAppointmentInfos
} from '@/graphql/appointment'
import { allUsers } from '@/graphql/users'
import { getDate } from '@/utils'
export default {
  name: 'CallOutForm',
  props: [
    'formVisible',
    'formTypes',
    'formType',
    'formData'
  ],
  data() {
    const client = process.env.VUE_APP_CLIENT
    const currentUser = JSON.parse(localStorage.getItem('user'))
    return {
      currentUser: currentUser,
      client: client,
      loading: false,
      titleForm: 'User',
      textMap: {
        add: '',
        edit: ''
      },
      appointment: {
        id: undefined,
        student_id: '',
        guidance_id: '',
        category_id: '',
        status: '',
        student_notes: '',
        date_booked: '',
        date_declined: '',
        decline_reason: '',
        date_approved: '',
        date_schedules: '',
        appointment_stab: '',
        college: '',
        yrSection: ''
      },
      listQuery: {
        first: 10,
        page: 1,
        search: ''
      },
      categoryOptions: [],
      rules: {
        category_id: [{ required: true, message: 'Category is required.', trigger: 'blur' }],
        student_notes: [{ required: true, message: 'Notes is required.', trigger: 'blur' }],
        college: [{ required: true, message: 'College is required.', trigger: 'blur' }],
        yrSection: [{ required: true, message: 'Year and Section is required.', trigger: 'blur' }]
      }
    }
  },
  watch: {
    formVisible: function() {
      this.getStudentData()
      if (this.formTypes) {
        if (this.formType === this.formTypes.add) {
          this.handleCreate()
        } else if (this.formType === this.formTypes.edit) {
          this.handleUpdate(this.formData)
        }
      }
    }
  },
  mounted() {
    this.textMap.add = `Add New Appointment`
    this.textMap.edit = `Edit Appointment`
    this.get()
  },
  methods: {
    getStudentData() {
      const query = {
        first: 1,
        page: 1,
        student: this.currentUser.email,
        registration_type: this.currentUser.registration_type,
        client: this.client
      }
      allUsers(query, (response, success) => {
        if (success) {
          const data = response.allUsers.data
          if (data.length > 0) {
            data.forEach(item => {
              this.appointment.college = item.guidance_info.college ?? ''
              this.appointment.yrSection = `${item.guidance_info.year} - ${item.guidance_info.section}` ?? ''
              this.appointment.student_id = item.id ?? ''
            })
            this.loading = false
          } else {
            this.loading = false
            this.errMessage(response.allUsers.message)
          }
        } else {
          this.loading = false
          this.errMessage(response.allUsers.message)
        }
      })
    },
    get() {
      this.loading = true
      this.categoryOptions = []
      categoryInfos(this.listQuery, (response, success) => {
        if (success) {
          const data = response.allCategory.data
          if (data.length > 0) {
            data.forEach(item => {
              const data = {
                id: item.id,
                label: `${item.name}`,
                value: `${item.id}`
              }
              this.categoryOptions = this.categoryOptions.concat(data)
              this.getStudentData()
            })
          } else {
            this.loading = false
            this.errMessage(response.allCategory.message)
          }
        } else {
          this.loading = false
          this.errMessage(response.allCategory.message)
        }
      })
    },
    errMessage(response) {
      this.$message({
        type: 'error',
        message: response
      })
      this.loading = false
    },
    handleCreate() {
      this.getStudentData()
    },
    create() {
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          this.appointment.status = 'P'
          this.appointment.date_booked = getDate(new Date())
          const query = {
            client: this.client,
            user_type: this.currentUser.registration_type,
            user_id: this.currentUser.id.toString(),
            action: 'update',
            data: this.appointment
          }
          createUpdateStudentGuidanceAppointmentInfos(query, (response, success) => {
            if (success) {
              this.$message({
                type: 'success',
                message: response.createUpdateStudentGuidanceAppointmentInfos.message
              })
              this.exitForm()
              this.successForm()
            } else {
              this.$message({
                type: 'error',
                message: response.createUpdateStudentGuidanceAppointmentInfos.message
              })
            }
          })
        } else {
          this.loading = false
          return false
        }
      })
    },
    handleUpdate(row) {
      this.getStudentData()
      this.appointment = row
      const description = this.categoryOptions.find((item) => item.id === row.category_id)
      this.appointment.category_id = description.label
    },
    update() {
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          const query = {
            client: this.client,
            user_type: this.currentUser.registration_type,
            user_id: this.currentUser.id.toString(),
            action: 'update',
            data: this.appointment
          }
          createUpdateStudentGuidanceAppointmentInfos(query, (response, success) => {
            if (success) {
              this.$message({
                type: 'success',
                message: response.createUpdateStudentGuidanceAppointmentInfos.message
              })
              this.exitForm()
              this.successForm()
            } else {
              this.$message({
                type: 'error',
                message: response.createUpdateStudentGuidanceAppointmentInfos.message
              })
            }
          })
        } else {
          this.loading = false
          return false
        }
      })
    },
    onSubmit() {
      if (this.formTypes) {
        if (this.formType === this.formTypes.add) {
          this.create()
        } else if (this.formType === this.formTypes.edit) {
          this.update()
        }
      }
    },
    resetModel() {
      this.appointment = {
        id: undefined,
        student_id: '',
        guidance_id: '',
        category_id: '',
        status: '',
        student_notes: '',
        date_booked: '',
        date_declined: '',
        decline_reason: '',
        date_approved: '',
        date_schedules: '',
        appointment_stab: '',
        college: '',
        yrSection: ''
      }
    },
    onClose() {
      this.exitForm()
    },
    exitForm() {
      this.resetModel()
      this.$emit('formVisible')
    },
    successForm() {
      this.$emit('formSuccess')
    }
  }
}
</script>

<style lang="scss" scoped>
.cancel {
  background-color: $cancel;
  color: gray;

}
</style>
