export function chatAllUser(query, callback) {
  if (apolloClientInstance) {
    apolloClientInstance.query({
      query: ChatAllUser,
      variables: query
    }).then(function(response) {
      callback(response.data, true)
    }).catch(function(response) {
      callback(response, false)
    })
  }
}

