<script>
export default {
  name: 'CallOutForm',
  props: [
    'formVisible',
    'formTypes',
    'formType',
    'formData'
  ],
  data() {
    return {
      loading: false,
      titleForm: 'Standard Form For Boat Registration',
      textMap: {
        add: '',
        edit: ''
      },
      dataForm: {
        id: undefined,
        imageUrl: '',
        registrationType: '',
        last_name: '',
        first_name: '',
        middle_name: '',
        suffix: '',
        address: '',
        barangay: '',
        homeport: '',
        fishing_vessel_name: '',
        fishing_vessel_type: '',
        built_place: '',
        built_year: '',
        material_used: '',
        fishing_vessel_dimension_length: '',
        fishing_vessel_dimension_breadth: '',
        fishing_vessel_dimension_depth: '',
        fishing_vessel_dimension_gross_tonnage: '',
        fishing_vessel_dimension_net_tonnage: '',
        engine_make: '',
        serial_number: '',
        horse_power: '',
        classification_fishing_gear_hook_and_line_id: '',
        classification_fishing_gear_pots_traps_id: '',
        classification_fishing_gear_seine_nets_id: '',
        classification_fishing_gear_gill_nets_id: '',
        classification_fishing_gear_falling_gear_id: '',
        classification_fishing_gear_trammed_net_id: '',
        classification_fishing_gear_lift_nets_id: '',
        classification_fishing_gear_scoop_nets_id: '',
        classification_fishing_gear_misc_fisher_gears_id: ''
      },
      rules: {},
      fileList: [],
      file: ''
    }
  },
  watch: {
    formVisible: function() {
      if (this.formTypes) {
        if (this.formType === this.formTypes.add) {
          this.handleCreate()
        } else if (this.formType === this.formTypes.edit) {
          this.handleUpdate(this.formData)
        }
      }
    }
  },
  mounted() {
    this.textMap.add = `Add New ${this.titleForm}`
    this.textMap.edit = `Edit ${this.titleForm}`
  },
  methods: {
    previewImage(event) {
      // Reference to the DOM input element
      var input = event.target
      // Ensure that you have a file before attempting to read it
      if (input.files && input.files[0]) {
        // create a new FileReader to read this image and convert to base64 format
        var reader = new FileReader()
        // Define a callback function to run, when FileReader finishes its job
        reader.onload = (e) => {
          // Note: arrow function used here, so that "this.imageData" refers to the imageData of Vue component
          // Read image as base64 and set to imageData
          this.dataForm.imageUrl = e.target.result
        }
        // Start the reader job - read file as a data url (base64 format)
        reader.readAsDataURL(input.files[0])
      }
    },
    handleCreate() {
      console.log('handleCreate')
      this.loading = false
    },
    handleUpdate(row) {
      console.log('handleUpdate', row)
      this.loading = false
    },
    onSubmit() {
      if (this.formTypes) {
        if (this.formType === this.formTypes.add) {
          this.create()
        } else if (this.formType === this.formTypes.edit) {
          this.update()
        }
      }
    },
    create() {
      this.$refs.dataForm.validate(valid => {
        if (valid) {
          this.loading = true
          this.loading = false
        }
      })
    },
    update() {},
    resetModel() {
      this.dataForm = {
        id: undefined,
        registrationType: ''
      }
      this.formVisible = false
    },
    exitForm() {
      this.resetModel()
      this.$emit('formVisible')
    },
    successForm() {
      this.$emit('formSuccess')
    }
  }
}
</script>

