<template>
  <div :class="{'has-logo':showLogo}">
    <logo v-if="showLogo" :collapse="isCollapse" />
    <el-image v-if="clientCheck === 'fishery'" :src="logo" />
    <div v-if="clientCheck === 'guidance'" style="text-align: center">
      <el-image :src="logo" style="width: 50%" />
    </div>
    <!--    <el-image  :src="logo" style="background-color: white; width: 30%" />-->
    <el-scrollbar wrap-class="scrollbar-wrapper">
      <el-menu
        :default-active="activeMenu"
        :collapse="isCollapse"
        :background-color="variables.menuBg"
        :text-color="variables.menuText"
        :unique-opened="false"
        :active-text-color="variables.menuActiveText"
        :collapse-transition="false"
        mode="vertical"
      >
        <sidebar-item v-for="route in routes" :key="route.path" :item="route" :base-path="route.path" />
        <el-menu-item v-if="sidebar.opened" @click.native="logout">Logout</el-menu-item>
      </el-menu>
    </el-scrollbar>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Logo from './Logo'
import SidebarItem from './SidebarItem'
import variables from '@/styles/variables.scss'

export default {
  components: { SidebarItem, Logo },
  data() {
    const checkLogo = process.env.VUE_APP_CLIENT
    const clientCheck = process.env.VUE_APP_CLIENT
    return {
      logo: require('@/assets/' + checkLogo + '/logo.png'),
      clientCheck: clientCheck
    }
  },
  computed: {
    ...mapGetters([
      'sidebar'
    ]),
    routes() {
      return this.$router.options.routes
    },
    activeMenu() {
      const route = this.$route
      const { meta, path } = route
      // if set path, the sidebar will highlight the path you set
      if (meta.activeMenu) {
        return meta.activeMenu
      }
      return path
    },
    showLogo() {
      return this.$store.state.settings.sidebarLogo
    },
    variables() {
      return variables
    },
    isCollapse() {
      console.log('this.sidebar.opened', this.sidebar.opened)
      return !this.sidebar.opened
    }
  },
  methods: {
    async logout() {
      this.$confirm('Are you sure that you want to logout ?', 'Warning', {
        confirmButtonText: 'OK',
        type: 'warning',
        center: true,
        showCancelButton: false
      }).then(() => {
        window.location.reload()
        localStorage.removeItem('user')
        localStorage.removeItem('access_token')
        this.$router.push(`/login?redirect=${this.$route.fullPath}`)
      })
    }
  }
}
</script>
