<template>
  <div class="dashboard-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <h3>{{ this.$route.name }}</h3>
        <el-dropdown style="margin-left: 1%; float: right;" @command="exportData">
          <el-button
            size="medium"
            class="filter-item button-primary actionSizes"
            icon="el-icon-download"
            :loading="printLoading"
          >
            Export as<i class="el-icon-arrow-down el-icon--right" />
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="xlsx">Excel</el-dropdown-item>
            <el-dropdown-item command="pdf">PDF</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-button
          v-if="hideMe === 'true'"
          class="button-primary actionSizes"
          style="margin-left: 1%; float: right;"
          size="medium"
          icon="el-icon-plus"
          @click="addNewRecord"
        >
          Add New Record
        </el-button>
        <div style="float: right;" class="actionSearchSizes">
          <el-input v-model="searchValue" size="medium" placeholder="search here" class="input-with-select" @keyup.enter.native="handleSearch">
            <el-button
              slot="append"
              style="background-color: #ff9897; color: white"
              icon="el-icon-search"
              class="btn-primary-color"
              @click="handleSearch"
            >Search
            </el-button>
          </el-input>
        </div>
      </div>
      <pagination
        v-show="total>0"
        :total="total"
        :page.sync="query.page"
        :limit.sync="query.first"
        @pagination="get"
      />
      <el-table
        v-if="currentUser.registration_type === 'Student'"
        v-loading="tableLoading"
        :header-cell-style="{ background: '#7c96a6', color: 'white' }"
        style="width: 100%"
        stripe
        border
        fit
        :data="pendingAppointments"
        height="650"
      >
        <el-table-column
          prop="id"
          type="index"
          label="#"
          :index="indexMethod"
          width="50"
          align="center"
        />
        <el-table-column
          label="Date Approved"
          width="auto"
        >
          <template slot-scope="{row}">
            {{ row.date_approved }}
          </template>
        </el-table-column>
        <el-table-column
          label="Category"
          width="auto"
        >
          <template slot-scope="{row}">
            {{ row.category_name }}
          </template>
        </el-table-column>
        <!--        <el-table-column-->
        <!--          label="Accuracy"-->
        <!--          width="auto"-->
        <!--        >-->
        <!--          View Accuracy-->
        <!--        </el-table-column>-->
        <el-table-column
          label="Note"
          width="auto"
        >
          <template slot-scope="{row}">
            {{ row.student_notes }}
          </template>
        </el-table-column>
        <el-table-column
          label="Date Booked"
          width="200"
        >
          <template slot-scope="{row}">
            <div>{{ formatDate(row.created_at) }}</div>
          </template>
        </el-table-column>
      </el-table>
      <el-table
        v-if="currentUser.registration_type === 'Guidance' || currentUser.registration_type === 'Teacher'"
        v-loading="tableLoading"
        :header-cell-style="{ background: '#7c96a6', color: 'white' }"
        style="width: 100%"
        stripe
        border
        fit
        :data="pendingAppointments"
        height="650"
      >
        <el-table-column
          prop="id"
          type="index"
          label="#"
          :index="indexMethod"
          width="50"
          align="center"
        />
        <el-table-column
          label="Complete"
          width="auto"
        >
          <template slot-scope="{row}">
            <div style="text-align: center">
              <el-tag type="success" @click="completeAppointment(row)">Complete</el-tag>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          label="Date Approved"
          width="auto"
        >
          <template slot-scope="{row}">
            {{ row.date_approved }}
          </template>
        </el-table-column>
        <el-table-column
          label="Date Schedule"
          width="auto"
        >
          <template slot-scope="{row}">
            {{ row.date_schedules }}
          </template>
        </el-table-column>
        <!--        <el-table-column-->
        <!--          label="Appointment Stab & Code"-->
        <!--          width="auto"-->
        <!--        >-->
        <!--          Click to Download-->
        <!--        </el-table-column>-->
        <el-table-column
          label="Student"
          width="200"
        >
          <template slot-scope="{row}">
            {{ row.student_name }}
          </template>
        </el-table-column>
        <el-table-column
          label="Year & Section"
          width="200"
        >
          <template slot-scope="{row}">
            <div>{{ row.guidance_other_info.year }}</div>
            <div>{{ row.guidance_other_info.section }}</div>
          </template>
        </el-table-column>
        <el-table-column
          label="Category"
          width="200"
        >
          <template slot-scope="{row}">
            {{ row.category_name }}
          </template>
        </el-table-column>
      </el-table>
      <div style="padding: 14px;">
        <pagination
          v-show="total>0"
          background
          :total="total"
          :page.sync="query.page"
          :limit.sync="query.first"
          @pagination="get"
        />
      </div>
    </el-card>
    <call-out-form
      :form-visible="formVisible"
      :form-types="formTypes"
      :form-type="formType"
      :form-data="formData"
      @formSuccess="get"
    />
  </div>
</template>

<script>
import Pagination from '@/components/Pagination'
import CallOutForm from '@/views/guidance/appointment-pending/Form'
import {
  allStudentGuidanceAppointmentInfos, createUpdateStudentGuidanceAppointmentInfos, deleteGuidanceAppointment
} from '@/graphql/appointment'
import { getDate } from '@/utils/index'
export default {
  name: 'PendingAppointment',
  components: {
    Pagination,
    CallOutForm
  },
  data() {
    const hideMe = process.env.VUE_APP_GUIDANCE_HIDE_SHOW_BUTTON
    const studentShowHideMe = process.env.VUE_APP_GUIDANCE_STUDENT_HIDE_SHOW_BUTTON
    const storage = JSON.parse(localStorage.getItem('user'))
    const client = process.env.VUE_APP_CLIENT
    return {
      client: client,
      hideMe: hideMe,
      studentShowHideMe: studentShowHideMe,
      formTypes: {
        add: 'add',
        edit: 'edit'
      },
      formType: '',
      formData: {},
      approvedDialog: false,
      formVisible: false,
      tableLoading: false,
      printLoading: false,
      searchValue: '',
      currentUser: storage,
      query: {
        first: 10,
        page: 1,
        search: '',
        appointmentStatus: 'A',
        user: '',
        user_type: ''
      },
      total: 0,
      pendingAppointments: [],
      appointment: {
        id: undefined,
        student_id: '',
        guidance_id: '',
        category_id: '',
        status: '',
        student_notes: '',
        date_booked: '',
        date_declined: '',
        decline_reason: '',
        date_approved: '',
        date_schedules: '',
        appointment_stab: '',
        college: '',
        yrSection: ''
      },
      tempAppointment: {},
      showDeclineReason: false,
      titleApproveDisapprove: ''
    }
  },
  watch: {
    searchValue: function() {
      if (this.searchValue === '') {
        this.handleSearch()
      }
    }
  },
  mounted() {
    this.get()
  },
  methods: {
    completeAppointment(row) {
      alert('asfa')
      this.titleApproveDisapprove = 'Schedule | Approved'
      // row.status = 'C'
      this.showDeclineReason = false
      this.approvedDialog = true
      this.tempAppointment = ''

      row.status = 'C'
      this.approvedDialog = true
      this.tempAppointment = row

      const query = {
        client: this.client,
        user_type: this.currentUser.registration_type,
        action: 'update',
        data: this.tempAppointment
      }
      this.approvedDisapproved(query)
    },
    approve() {
      this.tempAppointment.guidance_id = this.currentUser.id
      if (!this.showDeclineReason) {
        this.tempAppointment.date_approved = getDate(new Date())
        this.tempAppointment.date_schedules = getDate(this.appointment.date_approved)
      } else {
        this.tempAppointment.date_declined = getDate(new Date())
        this.tempAppointment.decline_reason = this.appointment.decline_reason
      }

      const query = {
        client: this.client,
        user_type: this.currentUser.registration_type,
        action: 'update',
        data: this.tempAppointment
      }
      this.approvedDisapproved(query)
    },
    handleClose() {
      this.get()
      this.approvedDialog = false
    },
    formatDate(date) {
      return getDate(date)
    },
    get() {
      this.tableLoading = true
      this.printLoading = true
      this.pendingAppointments = []
      this.query.user = this.currentUser.id.toString()
      this.query.user_type = this.currentUser.registration_type
      allStudentGuidanceAppointmentInfos(this.query, (response, success) => {
        if (success) {
          const data = response.allStudentGuidanceAppointmentInfos.data
          if (data.length > 0) {
            this.pendingAppointments = data
            this.total = response.allStudentGuidanceAppointmentInfos.paginator_info.total
            this.tableLoading = false
            this.printLoading = false
          } else {
            this.tableLoading = false
            this.printLoading = false
          }
        } else {
          this.errMessage()
        }
      })
    },
    errMessage(response) {
      this.$message({
        type: 'error',
        message: response
      })
      this.tableLoading = false
      this.printLoading = false
    },
    handleSearch() {
      this.query.page = 1
      this.query.search = this.searchValue
      this.get()
    },
    indexMethod(index) {
      return (index + 1) + ((this.query.page - 1) * this.query.first)
    },
    openDeleteQuestion(data) {
      this.$confirm('This will permanently delete the record. Continue?', 'Warning', {
        confirmButtonText: 'OK',
        type: 'warning',
        center: true,
        showCancelButton: false
      }).then(() => {
        deleteGuidanceAppointment({ id: data.id }, (response, success) => {
          if (success) {
            this.$message({
              type: 'success',
              message: response.deleteGuidanceAppointment.message
            })
            this.get()
          } else {
            this.$message({
              message: `Unable to retrieve the data.`,
              dangerouslyUseHTMLString: true,
              type: 'error'
            })
          }
        })
      })
    },
    addNewRecord() {
      this.formVisible = true
      this.formType = this.formTypes.add
      this.formData = {}
    },
    updateRecord(row) {
      this.formVisible = true
      this.formType = this.formTypes.edit
      this.formData = row
    },
    approvedRecord(row) {
      this.$confirm('Are you sure you want to approve the appointment?', 'Warning', {
        confirmButtonText: 'YES',
        type: 'warning',
        center: true,
        showCancelButton: true,
        cancelButtonText: 'No'
      }).then(() => {
        this.titleApproveDisapprove = 'Schedule | Approved'
        row.status = 'A'
        this.showDeclineReason = false
        this.approvedDialog = true
        this.tempAppointment = row

        const query = {
          client: this.client,
          user_type: this.currentUser.registration_type,
          action: 'update',
          data: this.tempAppointment
        }
        this.approvedDisapproved(query)
      })
    },
    declineRecord(row) {
      this.$confirm('Are you sure you want to decline the appointment?', 'Warning', {
        confirmButtonText: 'YES',
        type: 'warning',
        center: true,
        showCancelButton: true,
        cancelButtonText: 'No'
      }).then(() => {
        this.titleApproveDisapprove = 'Schedule | Declined'
        this.showDeclineReason = true
        row.status = 'D'
        this.approvedDialog = true
        this.tempAppointment = row

        const query = {
          client: this.client,
          user_type: this.currentUser.registration_type,
          action: 'update',
          data: this.tempAppointment
        }
        this.approvedDisapproved(query)
      })
    },
    approvedDisapproved(query) {
      createUpdateStudentGuidanceAppointmentInfos(query, (response, success) => {
        if (success) {
          this.approvedDialog = false
          this.showDeclineReason = false
          this.tempAppointment.decline_reason = ''
          this.appointment.decline_reason = ''
          this.$message({
            type: 'success',
            message: response.createUpdateStudentGuidanceAppointmentInfos.message
          })
          this.get()
        } else {
          this.approvedDialog = false
          this.showDeclineReason = false
          this.tempAppointment.decline_reason = ''
          this.appointment.decline_reason = ''
          this.$message({
            type: 'error',
            message: response.createUpdateStudentGuidanceAppointmentInfos.message
          })
        }
      })
    },
    exportData(type) {}
  }
}
</script>

