<template>
  <div class="dashboard-container">
    <el-row>
      <el-col v-if="chatList" :span="5">
        <el-card>
          <div style="padding: 1%">
            <el-autocomplete
              v-model="searchValue"
              style="width: 100%;"
              prefix-icon="el-icon-search"
              class="inline-input"
              :fetch-suggestions="querySearch"
              placeholder="search"
              clearable
              :trigger-on-focus="true"
              @select="handleSelect"
            >
              <template slot-scope="{ item }">
                <div>
                  {{ item.name }}
                </div>
              </template>
            </el-autocomplete>
          </div>
          <div class="chatList">
            <li v-for="user in chatItems" :key="user.id" class="list-item" style="list-style-type: none; width: 100%">
              <div v-if="findImage(user)" class="image-preview" @click="handleChatSelect(user)">
                <span><img class="preview" :src="findImage(user)"><p style="margin-top: -10%; margin-left: 15%">{{ findName(user) }}</p></span>
              </div>
              <div v-else class="image-preview" @click="handleChatSelect(user)">
                <span><el-avatar :size="40" :src="avatar" /><p style="margin-top: -10%; margin-left: 15%">{{ findName(user) }}</p></span>
              </div>
            </li>
          </div>
        </el-card>
      </el-col>
      <el-col :span="chatArea">
        <div v-if="chatName" style="background-color: #FFA500; padding: 1%">
          <el-row :gutter="20">
            <el-col :span="1"><div class="grid-content bg-purple">
              <img class="preview" :src="findImage(chatNameId)">
            </div></el-col>
            <el-col :span="6"><div class="grid-content bg-purple">
              {{ chatName }}
            </div></el-col>
          </el-row>
        </div>
        <div v-else style="background-color: #FFA500; padding: 1%">
          <el-row :gutter="20">
            <el-col :span="1"><div class="grid-content bg-purple">
              <img class="preview" :src="findImage(chatNameId)">
            </div></el-col>
            <el-col :span="6"><div class="grid-content bg-purple">
              {{ chatName }}
            </div></el-col>
          </el-row>
        </div>
        <el-card class="box-card">
          <div class="chat">
            <div v-for="message in messages" :key="message.id" class="chat__wrapper">
              <div v-if="message.user_id.toString() !== currentUser.id.toString()" class="chat__message">
                <div>{{ message.message }}</div>
              </div>
              <div v-else class="chat__message chat__message-own">
                <div>{{ message.message }}</div>
              </div>
            </div>
          </div>
        </el-card>
        <div class="box-card-footer full-width">
          <el-input v-model="messageInput" placeholder="Please input" @keyup.enter.native="sendMessage">
            <el-button slot="append" icon="el-icon-position" @click="sendMessage" />
          </el-input>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {
  chatAllUser,
  chatListUser,
  chatMessage
} from '@/graphql/chat'
export default {
  name: 'Index',
  data() {
    const storage = JSON.parse(localStorage.getItem('user'))
    const client = process.env.VUE_APP_CLIENT
    return {
      client: client,
      currentUser: storage,
      chatAllUserQuery: {
        client: '',
        user_id: ''
      },
      chatListUserQuery: {
        user_id: '',
        client: ''
      },
      chatMessageQuery: {
        channel_id: '',
        reciever_id: '',
        user_id: '',
        message: ''
      },
      avatar: 'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png',
      messageInput: '',
      imageUrl: false,
      chatList: false,
      chatArea: 24,
      count: 0,
      searchValue: '',
      links: [],
      allUser: [],
      chatItems: [],
      messages: [],
      chatName: '',
      chatNameId: '',
      channel_id: '',
      myInterval: 0
    }
  },
  mounted() {
    if (this.currentUser.registration_type !== '') {
      this.chatList = true
      this.chatArea = 19
    }
    this.getAllUser()
    this.getChatListUser()
    console.log('this.$route.name', this.$route.name)
    this.interval = setInterval(this.getRealTimeChat, 1000)
    this.interval = setInterval(this.getChatListUser, 1000)
  },
  destroyed() {
    // clearInterval(this.myInterval)
    window.clearTimeout(this.myInterval)
  },
  methods: {
    getAllUser() {
      this.chatAllUserQuery.client = this.client.toString()
      this.chatAllUserQuery.user_id = this.currentUser.id.toString()
      chatAllUser(this.chatAllUserQuery, (response, success) => {
        if (success) {
          this.allUser = response.chatAllUser.data
        }
      })
    },
    getChatListUser() {
      this.chatListUserQuery.user_id = this.currentUser.id.toString()
      this.chatListUserQuery.client = this.client.toString()
      chatListUser(this.chatListUserQuery, (response, success) => {
        if (success) {
          this.chatItems = response.chatListUser.data
        }
      })
    },
    findImage(user) {
      let findId = 0
      if (parseInt(this.currentUser.id) === parseInt(user.reciever_id)) {
        findId = user.user_id
      } else {
        findId = user.reciever_id
      }
      const description = this.allUser.find((item) => parseInt(item.id) === parseInt(findId))
      if (description !== undefined) {
        if (description.other_info !== null) {
          return description.other_info.photo ?? this.avatar
        } else {
          return this.avatar
        }
      } else {
        return this.avatar
      }
    },
    findName(user) {
      let findId = 0
      if (parseInt(this.currentUser.id) === parseInt(user.reciever_id)) {
        findId = user.user_id
      } else {
        findId = user.reciever_id
      }
      const description = this.allUser.find((item) => parseInt(item.id) === parseInt(findId))
      if (description !== undefined) {
        // return `${description.id} ${description.name}`
        return description.name
      } else {
        return 'guest'
      }
    },
    querySearch(queryString, cb) {
      const links = this.allUser
      const results = queryString ? links.filter(this.createFilter(queryString)) : links
      // call callback function to return suggestions
      cb(results)
    },
    createFilter(queryString) {
      return (link) => {
        return (link.name.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
      }
    },
    handleChatSelect(user) {
      this.messageInput = ''
      this.messages = []
      // this.handleSelect(user)
      let findId = 0
      if (parseInt(this.currentUser.id) !== parseInt(user.reciever_id)) {
        findId = user.reciever_id
      } else {
        findId = user.user_id
      }
      const item = this.allUser.find((item) => parseInt(item.id) === parseInt(findId))
      if (item !== undefined) {
        this.chatName = item.name
        this.chatNameId = item.id
        this.chatMessageQuery.channel_id = `${item.id}_${this.currentUser.id}`
        this.chatMessageQuery.reciever_id = item.id.toString()
        this.chatMessageQuery.user_id = this.currentUser.id.toString()
      }
      this.scrollToElement()
    },
    getRealTimeChat() {
      if (this.$route.name === 'chat-room') {
        setInterval(function() {
          chatMessage(this.chatMessageQuery, (response, success) => {
            if (success) {
              this.messages = response.chatMessage.data
            }
          })
        }.bind(this), 1000)
      } else {
        clearInterval(this.myInterval)
      }
    },
    handleSelect(item) {
      clearInterval(this.myInterval)
      this.messages = []
      this.chatName = item.name
      this.chatNameId = item.id
      this.chatMessageQuery.channel_id = `${item.id}_${this.currentUser.id}`
      this.chatMessageQuery.reciever_id = item.id.toString()
      this.chatMessageQuery.user_id = this.currentUser.id.toString()
      this.messageInput = ''
      this.scrollToElement()
    },
    sendMessage() {
      this.chatMessageQuery.message = this.messageInput
      chatMessage(this.chatMessageQuery, (response, success) => {
        if (success) {
          this.messageInput = ''
          this.chatMessageQuery.message = ''
          this.messages = []
          this.messages = response.chatMessage.data
          this.scrollToElement()
        }
      })
      this.getChatListUser()
    },
    scrollToElement() {
      const el = this.$refs.chat
      if (el) {
        el.scrollIntoView({ behavior: 'smooth' })
      }
    }
  }
}
</script>

