<script>
export default {
  name: 'CallOutForm',
  props: [
    'formVisible',
    'formTypes',
    'formType',
    'formData'
  ],
  data() {
    return {
      loading: false,
      titleForm: 'Application For Municipal FisherFolks Registration',
      textMap: {
        add: '',
        edit: ''
      },
      dataForm: {
        id: undefined,
        imageUrl: '',
        fishery_other_info_id: '',
        user_title: '',
        last_name: '',
        first_name: '',
        middle_name: '',
        suffix: '',
        address: '',
        barangay: '',
        contact_number: '',
        residence_year: '',
        age: '',
        dob: '',
        pob: '',
        gender: '',
        civil_status: '',
        no_children: '',
        nationality: '',
        educational_background_id: '',
        contact_person: '',
        contact_person_relationship: '',
        contact_person_contact_number: '',
        contact_person_address: '',
        organizationTableData: []
      },
      certify: false,
      rules: {}
    }
  },
  watch: {
    formVisible: function() {
      if (this.formTypes) {
        if (this.formType === this.formTypes.add) {
          this.handleCreate()
        } else if (this.formType === this.formTypes.edit) {
          this.handleUpdate(this.formData)
        }
      }
    }
  },
  mounted() {
    this.textMap.add = `Add New ${this.titleForm}`
    this.textMap.edit = `Edit ${this.titleForm}`
  },
  methods: {
    previewImage(event) {
      // Reference to the DOM input element
      var input = event.target
      // Ensure that you have a file before attempting to read it
      if (input.files && input.files[0]) {
        // create a new FileReader to read this image and convert to base64 format
        var reader = new FileReader()
        // Define a callback function to run, when FileReader finishes its job
        reader.onload = (e) => {
          // Note: arrow function used here, so that "this.imageData" refers to the imageData of Vue component
          // Read image as base64 and set to imageData
          this.dataForm.imageUrl = e.target.result
        }
        // Start the reader job - read file as a data url (base64 format)
        reader.readAsDataURL(input.files[0])
      }
    },
    handleCreate() {
      console.log('handleCreate')
      this.loading = false
    },
    handleUpdate(row) {
      console.log('handleUpdate', row)
      this.loading = false
    },
    onSubmit() {
      if (this.formTypes) {
        if (this.formType === this.formTypes.add) {
          this.create()
        } else if (this.formType === this.formTypes.edit) {
          this.update()
        }
      }
    },
    create() {
      this.$refs.dataForm.validate(valid => {
        if (valid) {
          this.loading = true
          this.loading = false
        }
      })
    },
    addGroup() {
      this.dataForm.organizationTableData.push({
        org_name: '',
        member_since: '',
        designation: ''
      })
    },
    deleteGroup(index) {
      this.dataForm.organizationTableData.splice(index, 1)
    },
    update() {},
    resetModel() {
      this.dataForm = {
        id: undefined,
        registrationType: ''
      }
      this.formVisible = false
    },
    exitForm() {
      this.resetModel()
      this.$emit('formVisible')
    },
    successForm() {
      this.$emit('formSuccess')
    }
  }
}
</script>

