import { apolloClientInstance } from '@/vue-apollo'
import RegistrationInfo from '@/graphql/fishery/RegistrationInfo.graphql'

export function registrationInfo(query, callback) {
  if (apolloClientInstance) {
    apolloClientInstance.query({
      query: RegistrationInfo,
      variables: query
    }).then(function(response) {
      callback(response.data, true)
    }).catch(function(response) {
      callback(response, false)
    })
  }
}
