import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

// import different route files
const client = process.env.VUE_APP_CLIENT
let clientConstantRoutes = ''
if (client === 'fishery') {
  const currentUser = JSON.parse(localStorage.getItem('user'))
  if (currentUser !== (null || '') && client === 'fishery') {
    if (currentUser?.registration_type === 'Fisherman') {
      clientConstantRoutes = require('./' + client + '/user')
    } else {
      clientConstantRoutes = require('./' + client + '/constant_routes')
    }
  }
} else if (client === 'unified') {
  const currentUser = JSON.parse(localStorage.getItem('user'))
  if (currentUser !== (null || '') && client === 'unified') {
    if (currentUser?.registration_type === 'Student') {
      clientConstantRoutes = require('./' + client + '/user')
    } else {
      clientConstantRoutes = require('./' + client + '/constant_routes')
    }
  }
} else {
  clientConstantRoutes = require('./' + client + '/constant_routes')
}

const clientModuleRoutes = require('./' + client + '/modules')
export const constantRoutes = [
  ...clientConstantRoutes.router
]
export const asyncRoutes = [
  ...clientModuleRoutes.router
]
const createRouter = () => new VueRouter({
  base: process.env.BASE_URL,
  routes: constantRoutes
})
const router = createRouter()

export function resetRouter() {
  const newRouter = createRouter()
  router.matcher = newRouter.matcher
}
export default router
