<template>
  <el-dialog
    width="33%"
    :title="textMap[formType]"
    :visible="formVisible"
    :before-close="exitForm"
    :destroy-on-close="true"
    :close-on-click-modal="false"
  >
    <el-form
      ref="dataForm"
      v-loading="loading"
      :rules="rules"
      :model="appointment"
      label-position="top"
    >
      <el-form-item label="College">
        <el-input v-model="appointment.college" disabled />
      </el-form-item>
      <el-form-item label="Year and Section">
        <el-input v-model="appointment.yrSection" disabled />
      </el-form-item>
      <el-form-item label="Category" prop="category">
        <el-select v-model="appointment.category" style="width: 100%;" filterable>
          <el-option
            v-for="item in categoryOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="Notes" prop="notes">
        <el-input v-model="appointment.notes" type="textarea" />
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button class="cancel" size="medium" @click="formVisible = false">CANCEL</el-button>
      <el-button size="medium" @click="onSubmit">SAVE</el-button>
    </span>
  </el-dialog>

</template>

<script>
export default {
  name: 'CallOutForm',
  props: [
    'formVisible',
    'formTypes',
    'formType',
    'formData'
  ],
  data() {
    return {
      loading: true,
      titleForm: 'User',
      textMap: {
        add: '',
        edit: ''
      },
      appointment: {
        id: undefined,
        name: '',
        email: '',
        created_by: '',
        updated_by: '',
        category: '',
        notes: '',
        college: '',
        yrSection: ''
      },
      categoryOptions: [],
      rules: {
        category: [{ required: true, trigger: 'blur' }],
        notes: [{ required: true, trigger: 'blur' }]
      }
    }
  },
  watch: {
    formVisible: function() {
      if (this.formTypes) {
        if (this.formType === this.formTypes.add) {
          this.handleCreate()
        } else if (this.formType === this.formTypes.edit) {
          this.handleUpdate(this.formData)
        }
      }
    }
  },
  mounted() {
    this.textMap.add = `Add New Appointment`
    this.textMap.edit = `Edit Appointment`
  },
  methods: {
    handleCreate() {
      console.log('handleUpdate')
      this.loading = false
    },
    handleUpdate(row) {
      console.log('handleUpdate', row)
      this.loading = false
    },
    onSubmit() {
      this.$refs['dataForm'].validate((valid) => {
        if (valid) {
          console.log('test')
        } else {
          this.loading = false
          return false
        }
      })
    },
    resetModel() {},
    exitForm() {
      this.resetModel()
      this.$emit('formVisible')
    },
    successForm() {
      this.$emit('formSuccess')
    }
  }
}
</script>

<style lang="scss" scoped>
.cancel {
  background-color: $cancel;
  color: gray;

}
</style>
