<template>
  <div class="login-container">
    <el-form
      ref="refRegisterForm"
      :model="registerForm"
      :rules="loginRules"
      class="login-form"
      auto-complete="off"
      label-position="left"
    >
      <div class="title-container">
        <div class="logo">
          <img width="100%" src="@/assets/unified/unified.jpg">
        </div>
      </div>

      <el-form-item prop="name">
        <span class="svg-container">
          <svg-icon icon-class="user" />
        </span>
        <el-input
          ref="name"
          v-model="registerForm.name"
          placeholder="Enter name"
          name="name"
          type="text"
          tabindex="1"
          auto-complete="off"
        />
      </el-form-item>

      <el-form-item prop="email">
        <span class="svg-container">
          <svg-icon icon-class="user" />
        </span>
        <el-input
          ref="email"
          v-model="registerForm.email"
          placeholder="Enter email"
          name="email"
          type="text"
          tabindex="2"
          auto-complete="off"
        />
      </el-form-item>

      <el-form-item prop="password">
        <span class="svg-container">
          <svg-icon icon-class="password" />
        </span>
        <el-input
          :key="passwordType"
          ref="password"
          v-model="registerForm.password"
          :type="passwordType"
          placeholder="Password"
          name="password"
          tabindex="2"
          auto-complete="on"
          @keyup.enter.native="handleRegister"
        />
        <span class="show-pwd" @click="showPwd">
          <svg-icon :icon-class="passwordType === 'password' ? 'eye' : 'eye-open'" />
        </span>
      </el-form-item>

      <el-form-item prop="password_confirmation">
        <span class="svg-container">
          <svg-icon icon-class="password" />
        </span>
        <el-input
          :key="passwordType"
          ref="password"
          v-model="registerForm.password_confirmation"
          :type="passwordType"
          placeholder="Confirm password"
          name="password_confirmation"
          tabindex="3"
          auto-complete="off"
          @keyup.enter.native="handleRegister"
        />
        <span class="show-pwd" @click="showPwd">
          <svg-icon :icon-class="passwordType === 'password' ? 'eye' : 'eye-open'" />
        </span>
      </el-form-item>
      <el-button
        :loading="loading"
        type="primary"
        style="width:100%;margin-bottom:30px;"
        @click.native.prevent="handleRegister"
      >SUBMIT</el-button>
      <span style="font-size: 16px">
        <router-link :to="{ path: '/'}" style="float: right; margin-top: 2%">Back to login</router-link>
      </span>
    </el-form>
  </div>
</template>

<script>
import {
  register,
  userUpdateRegistrationType
} from '@/graphql/auth'

export default {
  name: 'Register',
  data() {
    const client = process.env.VUE_APP_CLIENT
    return {
      client: client,
      registerForm: {
        name: '',
        email: '',
        password: '',
        password_confirmation: '',
        registration_type: 'Administrator'
      },
      loginRules: {
        name: [{ required: true, trigger: 'blur' }],
        email: [{ required: true, trigger: 'blur' }],
        password: [{ required: true, trigger: 'blur' }],
        password_confirmation: [{ required: true, trigger: 'blur' }],
        registration_type: [{ required: false, trigger: 'blur' }]
      },
      loading: false,
      passwordType: 'password',
      redirect: undefined
    }
  },
  watch: {
    $route: {
      handler: function(route) {
        this.redirect = route.query && route.query.redirect
      },
      immediate: true
    }
  },
  methods: {
    showPwd() {
      if (this.passwordType === 'password') {
        this.passwordType = ''
      } else {
        this.passwordType = 'password'
      }
      this.$nextTick(() => {
        this.$refs.password.focus()
      })
    },
    handleRegister() {
      this.$refs.refRegisterForm.validate(valid => {
        if (valid) {
          this.loading = true
          register(this.registerForm, (response, success) => {
            const res = response
            if (success) {
              const data = {
                id: parseInt(res.register.tokens.user.id),
                registration_type: this.registerForm.registration_type,
                client: this.client
              }
              console.log('$client', this.client)
              userUpdateRegistrationType(data, (response, success) => {
                if (success) {
                  const userData = response.userUpdateRegistrationType.data
                  if (userData) {
                    localStorage.setItem('access_token', res.register.tokens.access_token)
                    localStorage.setItem('user', JSON.stringify(userData))
                    this.$router.push({ path: '/' })
                  }
                } else {
                  this.$message({
                    message: response,
                    dangerouslyUseHTMLString: true,
                    type: 'error'
                  })
                }
              })
            } else {
              this.$message({
                message: response,
                dangerouslyUseHTMLString: true,
                type: 'error'
              })
            }
          })
          this.loading = false
        } else {
          this.loading = false
          return false
        }
      })
    }
  }
}
</script>

