<script>
import {
  createUpdateStudentGuidanceAppointmentInfos
} from '@/graphql/appointment'
import {
  categoryInfos
} from '@/graphql/categories'
export default {
  name: 'FilterForm',
  components: {
  },
  props: [
    'filterFormVisible',
    'filterFormData'
  ],
  data() {
    const client = process.env.VUE_APP_CLIENT
    const currentUser = JSON.parse(localStorage.getItem('user'))
    return {
      currentUser: currentUser,
      client: client,
      survey: {
        id: undefined,
        student_name: '',
        student_number: '',
        student_phone: '',
        date: '',
        radioQ1: '',
        radioQ2: '',
        radioQ3: '',
        radioQ4: '',
        radioQ5: '',
        radioQ6: '',
        radioQ7: '',
        radioQ8: '',
        radioQ9: '',
        radioQ10: '',
        radioQ11: '',
        radioQ12: '',
        radioQ13: '',
        radioQ14: '',
        radioQ15: '',
        radioQ16: '',
        radioQ17: '',
        radioQ18: '',
        radioQ19: '',
        radioQ20: '',
        radioQ21: '',
        radioQ22: '',
        radioQ23: '',
        radioQ24: '',
        radioQ25: '',
        radioQ26: '',
        radioQ27: '',
        radioQ28: '',
        radioQ29: '',
        textArea1: '',
        category_id: '',
        status: ''
      },
      categoryOptions: [],
      rules: {
        category_id: [{ required: true, message: 'Category is required.', trigger: 'blur' }],
        student_notes: [{ required: true, message: 'Notes is required.', trigger: 'blur' }],
        college: [{ required: true, message: 'College is required.', trigger: 'blur' }],
        yrSection: [{ required: true, message: 'Year and Section is required.', trigger: 'blur' }]
      }
    }
  },
  watch: {
    filterFormVisible: function(newVal) {
      if (newVal) {
        this.survey.id = parseInt(this.filterFormData.id)
        this.survey.student_name = this.filterFormData.student_name ?? this.filterFormData.name
        this.survey.student_number = this.filterFormData.other_info ? this.filterFormData.other_info.student_number : ''
        this.survey.student_phone = this.filterFormData.contact_info ? this.filterFormData.contact_info.mobile_number : ''
        this.survey.date = new Date()
        Object.assign(this.survey, JSON.parse(this.filterFormData.survey))
      }
    }
  },
  mounted() {
    this.getCategory()
  },
  methods: {
    getCategory() {
      categoryInfos({
        first: 10,
        page: 1,
        search: ''
      }, (response, success) => {
        if (success) {
          const data = response.allCategory.data
          if (data.length > 0) {
            data.forEach(item => {
              const data = {
                id: item.id,
                label: `${item.name}`,
                value: `${item.id}`
              }
              this.categoryOptions = this.categoryOptions.concat(data)
            })
          } else {
            this.loading = false
            this.errMessage(response.allCategory.message)
          }
        } else {
          this.loading = false
          this.errMessage(response.allCategory.message)
        }
      })
    },
    formClose() {
      this.reset()
      this.$emit('formSuccess')
    },
    submit() {
      this.filterFormData.status = 'S'
      this.filterFormData.category_id = this.survey.category_id
      this.filterFormData.survey = JSON.stringify(this.survey)
      const query = {
        client: this.client,
        user_type: this.currentUser.registration_type,
        user_id: this.currentUser.id.toString(),
        action: this.currentUser.registration_type === 'Student' ? 'create' : 'update',
        data: this.filterFormData
      }
      console.log('query', query)
      createUpdateStudentGuidanceAppointmentInfos(query, (response, success) => {
        if (success) {
          this.$message({
            type: 'success',
            message: response.createUpdateStudentGuidanceAppointmentInfos.message
          })
          this.reset()
          this.$emit('formSuccess')
        } else {
          this.$message({
            type: 'error',
            message: response.createUpdateStudentGuidanceAppointmentInfos.message
          })
        }
      })
    },
    reset() {
      this.filterFormData.survey = null
      this.survey = {
        id: undefined,
        student_name: '',
        student_number: '',
        student_phone: '',
        date: '',
        radioQ1: '',
        radioQ2: '',
        radioQ3: '',
        radioQ4: '',
        radioQ5: '',
        radioQ6: '',
        radioQ7: '',
        radioQ8: '',
        radioQ9: '',
        radioQ10: '',
        radioQ11: '',
        radioQ12: '',
        radioQ13: '',
        radioQ14: '',
        radioQ15: '',
        radioQ16: '',
        radioQ17: '',
        radioQ18: '',
        radioQ19: '',
        radioQ20: '',
        radioQ21: '',
        radioQ22: '',
        radioQ23: '',
        radioQ24: '',
        radioQ25: '',
        radioQ26: '',
        radioQ27: '',
        radioQ28: '',
        radioQ29: '',
        textArea1: '',
        category_id: ''
      }
    }
  }
}
</script>

