import Layout from '@/layout'
import Login from '@/views/fishery/Login/'
import Register from '@/views/fishery/Register/'
import Dashboard from '@/views/fishery/dashboard'
import Profile from '@/views/fishery/profile'
import ChangePassword from '@/views/fishery/change-password'
export const router = [
  {
    path: '/login',
    component: Login,
    hidden: true
  },
  {
    path: '/register',
    component: Register,
    hidden: true
  },
  {
    path: '/',
    component: Layout,
    redirect: '/dashboard',
    children: [
      {
        path: '/dashboard',
        component: Dashboard,
        name: 'Dashboard',
        meta: {
          title: 'Dashboard',
          roles: ['USER']
        }
      }
    ]
  },
  {
    path: '/profile',
    component: Layout,
    redirect: '/profile',
    children: [
      {
        path: '/profile',
        name: 'Profile',
        component: Profile,
        meta: {
          title: 'Profile',
          roles: ['USER']
        }
      }
    ]
  },
  {
    path: '/change-password',
    component: Layout,
    redirect: '/change-password',
    children: [
      {
        path: '/change-password',
        name: 'change-password',
        component: ChangePassword,
        meta: {
          title: 'Change Password',
          roles: ['USER']
        }
      }
    ]
  }
]
