<template>
  <div class="dashboard-container">
    <el-card class="box-card">
      <el-form
        ref="profileValidateForm"
        v-loading="formLoading"
        label-position="top"
        :model="userProfile"
        label-width="100px"
        class="demo-ruleForm"
      >
        <h3>{{ this.$route.name }}</h3>
        <el-form-item
          class="formField"
        >
          <div v-if="userProfile.imageUrl" class="image-preview">
            <img class="preview" :src="userProfile.imageUrl">
          </div>
          <div v-else class="image-preview">
            <img class="preview" src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png">
          </div>
          <div class="file-upload-form">
            <label class="custom-file-upload" style="color: white">
              <input type="file" accept="image/*" @change="previewImage">
              Upload Profile
            </label>
          </div>
        </el-form-item>

        <el-form-item
          label="First Name"
          prop="name"
        >
          <el-input v-model="userProfile.first_name" class="formField" />
        </el-form-item>

        <el-form-item
          label="Middle Name"
          prop="name"
        >
          <el-input v-model="userProfile.middle_name" class="formField" />
        </el-form-item>

        <el-form-item
          label="Last Name"
          prop="name"
        >
          <el-input v-model="userProfile.last_name" class="formField" />
        </el-form-item>

        <el-form-item
          label="Email"
          prop="email"
        >
          <el-input v-model="userProfile.email" class="formField" />
        </el-form-item>

        <!--        <el-form-item-->
        <!--          label="Password"-->
        <!--          prop="password"-->
        <!--        >-->
        <!--          <el-input v-model="userProfile.password" class="formField" type="password" />-->
        <!--        </el-form-item>-->

        <el-form-item
          label="Phone No"
          prop="contact"
        >
          <el-input v-model="userProfile.mobile_number" class="formField" />
        </el-form-item>

        <el-form-item
          label="Address"
          prop="address"
        >
          <el-input v-model="userProfile.mailing_address" class="formField" />
        </el-form-item>

        <el-form-item
          label="Birthday"
          prop="address"
        >
          <el-input v-model="userProfile.dob" class="formField" />
        </el-form-item>

        <el-form-item
          label="Age"
          prop="address"
        >
          <el-input v-model="userProfile.age" class="formField" />
        </el-form-item>

        <el-form-item
          label="Gender"
          prop="address"
        >
          <el-input v-model="userProfile.gender" class="formField" />
        </el-form-item>

        <el-form-item>
          <el-button class="button-primary" @click.native.prevent="handleSubmit">Save</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { allUsers, createUpdateProfile } from '@/graphql/users'

export default {
  name: 'Profile',
  data() {
    const client = process.env.VUE_APP_CLIENT
    return {
      formLoading: true,
      client: client,
      currentUser: JSON.parse(localStorage.getItem('user')),
      userProfile: {
        id: undefined,
        email: '',
        client: '',
        user_type: '',
        imageUrl: '',
        mailing_address: '',
        academic_status: '',
        student_number: '',
        parent_info_id: 0,
        first_name: '',
        last_name: '',
        middle_name: '',
        dob: '',
        age: '',
        gender: '',
        mobile_number: '',
        password: '',
        phone_number: ''
      }
    }
  },
  mounted() {
    this.viewData()
  },
  methods: {
    viewData() {
      this.formLoading = true
      this.userProfile.id = this.currentUser.id
      this.userProfile.email = this.currentUser.email
      const query = {
        first: 1,
        page: 1,
        student: this.currentUser.email,
        registration_type: this.currentUser.registration_type,
        client: this.client
      }
      allUsers(query, (response, success) => {
        if (success) {
          this.formLoading = false
          const data = response.allUsers.data
          console.log('data', data)
          if (data.length > 0) {
            data.forEach(item => {
              this.userProfile = {
                id: item.id ?? '',
                client: item.client ?? '',
                email: item.email ?? '',
                user_type: item.client ?? '',
                imageUrl: item.other_info.photo ?? '',
                mailing_address: item.other_info.mailing_address ?? '',
                academic_status: item.other_info.academic_status ?? '',
                student_number: item.other_info.student_number ?? '',
                parent_info_id: item.other_info.parent_info_id ?? '0',
                first_name: item.other_info.first_name ?? '',
                last_name: item.other_info.last_name ?? '',
                middle_name: item.other_info.middle_name ?? '',
                dob: item.other_info.dob ?? '',
                age: item.other_info.age ?? '',
                gender: item.other_info.gender ?? '',
                mobile_number: item.contact_info.mobile_number ?? '',
                password: item.other_info.password ?? '',
                phone_number: item.contact_info.phone_number ?? ''
              }
            })
          } else {
            this.formLoading = false
          }
        } else {
          this.formLoading = false
        }
      })
    },
    previewImage(event) {
      // Reference to the DOM input element
      var input = event.target
      // Ensure that you have a file before attempting to read it
      if (input.files && input.files[0]) {
        // create a new FileReader to read this image and convert to base64 format
        var reader = new FileReader()
        // Define a callback function to run, when FileReader finishes its job
        reader.onload = (e) => {
          // Note: arrow function used here, so that "this.imageData" refers to the imageData of Vue component
          // Read image as base64 and set to imageData
          this.userProfile.imageUrl = e.target.result
        }
        // Start the reader job - read file as a data url (base64 format)
        reader.readAsDataURL(input.files[0])
      }
    },
    handleSubmit() {
      this.formLoading = true
      const query = {
        client: this.client,
        user_type: this.currentUser.registration_type,
        data: this.userProfile
      }
      createUpdateProfile(query, (response, success) => {
        if (success) {
          this.$message({
            type: 'success',
            message: response.createUpdateProfile.message
          })
          this.resetModel()
        } else {
          this.$message({
            type: 'error',
            message: response.createUpdateProfile.message
          })
          this.resetModel()
        }
      })
    },
    resetModel() {
      this.userProfile = {
        id: undefined,
        email: '',
        client: '',
        user_type: '',
        imageUrl: '',
        mailing_address: '',
        academic_status: '',
        student_number: '',
        parent_info_id: 0,
        first_name: '',
        last_name: '',
        middle_name: '',
        dob: '',
        age: '',
        gender: '',
        mobile_number: '',
        password: '',
        phone_number: ''
      }
      this.viewData()
    }
  }
}
</script>

