import Layout from '@/layout'
import Login from '@/views/guidance/Login/'
import Register from '@/views/guidance/Register/'
import Dashboard from '@/views/guidance/dashboard'
import AppointmentApproved from '@/views/guidance/appointment-approved'
import AppointmentPending from '@/views/guidance/appointment-pending'
import AppointmentCancelled from '@/views/guidance/appointment-cancelled'
import Profile from '@/views/guidance/profile'
import Chat from '@/views/guidance/chat'
export const router = [
  {
    path: '/login',
    component: Login,
    hidden: true
  },
  {
    path: '/register',
    component: Register,
    hidden: true
  },
  {
    path: '/',
    component: Layout,
    redirect: '/dashboard',
    children: [
      {
        path: '/dashboard',
        component: Dashboard,
        name: `Dashboard`,
        meta: {
          title: `Dashboard`,
          roles: ['ADMIN']
        }
      }
    ]
  },
  {
    path: '/profile',
    component: Layout,
    redirect: '/profile',
    children: [
      {
        path: '/profile',
        component: Profile,
        name: `Profile`,
        meta: {
          title: `Profile`,
          roles: ['ADMIN']
        }
      }
    ]
  },
  {
    path: '/appointments',
    component: Layout,
    name: 'Appointments',
    meta: {
      title: 'Appointment',
      roles: ['ADMIN']
    },
    children: [
      {
        path: '/pending-appointment',
        name: 'Pending appointment',
        component: AppointmentPending,
        meta: { title: 'Pending' }
      },
      {
        path: '/approved-appointment',
        name: 'Approved appointment',
        component: AppointmentApproved,
        meta: { title: 'Approved' }
      },
      {
        path: '/canceled-appointment',
        name: 'Canceled Appointment',
        component: AppointmentCancelled,
        meta: { title: 'Canceled' }
      }
    ]
  },
  {
    path: '/messages',
    component: Layout,
    name: 'Messages',
    meta: {
      title: 'Message',
      roles: ['ADMIN']
    },
    children: [
      {
        path: '/chat-room',
        name: 'chat-room',
        component: Chat,
        meta: { title: 'Chat Room' }
      }
    ]
  }
]
