<script>
import Pagination from '@/components/Pagination'
import CallOutForm from '@/views/fishery/folks/Form'
import {
  deleteUser
} from '@/graphql/users/'
import {
  registrationInfo
} from '@/graphql/fishery'
export default {
  name: 'Dashboard',
  components: {
    Pagination,
    CallOutForm
  },
  data() {
    return {
      loading: false,
      formTypes: {
        add: 'add',
        edit: 'edit'
      },
      formType: '',
      formData: {},
      formVisible: false,
      tableLoading: false,
      printLoading: false,
      searchValue: '',
      currentUser: JSON.parse(localStorage.getItem('user')),
      query: {
        first: 10,
        page: 1,
        search: '',
        user: '',
        user_type: '',
        registration_type: ''
      },
      total: 0,
      registeredFolks: [],
      dataForm: {
        id: undefined,
        imageUrl: '',
        fishery_other_info_id: '',
        user_title: '',
        last_name: '',
        first_name: '',
        middle_name: '',
        suffix: '',
        address: '',
        barangay: '',
        contact_number: '',
        residence_year: '',
        age: '',
        dob: '',
        pob: '',
        gender: '',
        civil_status: '',
        no_children: '',
        nationality: '',
        educational_background_id: '',
        contact_person: '',
        contact_person_relationship: '',
        contact_person_contact_number: '',
        contact_person_address: '',
        organizationTableData: []
      },
      certify: false,
      rules: {}
    }
  },
  watch: {
    searchValue: function() {
      if (this.searchValue === '') {
        this.handleSearch()
      }
    }
  },
  mounted() {
    this.get()
  },
  methods: {
    onSubmit() {
      if (this.formTypes) {
        if (this.formType === this.formTypes.add) {
          this.create()
        } else if (this.formType === this.formTypes.edit) {
          this.update()
        }
      }
    },
    previewImage(event) {
      // Reference to the DOM input element
      var input = event.target
      // Ensure that you have a file before attempting to read it
      if (input.files && input.files[0]) {
        // create a new FileReader to read this image and convert to base64 format
        var reader = new FileReader()
        // Define a callback function to run, when FileReader finishes its job
        reader.onload = (e) => {
          // Note: arrow function used here, so that "this.imageData" refers to the imageData of Vue component
          // Read image as base64 and set to imageData
          this.dataForm.imageUrl = e.target.result
        }
        // Start the reader job - read file as a data url (base64 format)
        reader.readAsDataURL(input.files[0])
      }
    },
    addGroup() {
      this.dataForm.organizationTableData.push({
        org_name: '',
        member_since: '',
        designation: ''
      })
    },
    deleteGroup(index) {
      this.dataForm.organizationTableData.splice(index, 1)
    },
    get() {
      this.formVisible = false
      this.tableLoading = true
      this.registeredFolks = []
      registrationInfo(this.query, (response, success) => {
        if (success) {
          const data = response.registrationInfo.data
          console.log('data', data)
          if (data.length > 0) {
            this.registeredFolks = data
            this.dataForm = data

            this.tableLoading = false
          } else {
            this.tableLoading = false
          }
        } else {
          this.tableLoading = false
          this.$message({
            message: `Unable to retrieve the data.`,
            dangerouslyUseHTMLString: true,
            type: 'error'
          })
        }
      })
    },
    handleSearch() {
      this.query.page = 1
      this.query.search = this.searchValue.toString()
      this.query.user = this.currentUser.id.toString()
      this.query.user_type = this.currentUser.registration_type.toString()
      this.query.registration_type = this.$route.name.toString()
      this.get()
    },
    indexMethod(index) {
      return (index + 1) + ((this.query.page - 1) * this.query.first)
    },
    openDeleteQuestion(data) {
      this.$confirm('This will permanently delete the record. Continue?', 'Warning', {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
        type: 'warning',
        center: true
      }).then(() => {
        deleteUser({ id: data.id }, (response, success) => {
          if (success) {
            this.$message({
              type: 'success',
              message: response.deleteUser.message
            })
            this.get()
          } else {
            this.$message({
              message: `Unable to retrieve the data.`,
              dangerouslyUseHTMLString: true,
              type: 'error'
            })
          }
        })
      })
    },
    addNewRecord() {
      this.formVisible = true
      this.formType = this.formTypes.add
      this.formData = {}
    },
    updateRecord(row) {
      this.formVisible = true
      this.formType = this.formTypes.edit
      this.formData = row
    },
    exportData(type) {}
  }
}
</script>

