<template>
  <div class="dashboard-container">
    <el-card class="box-card">
      <el-form
        ref="profileValidateForm"
        v-loading="formLoading"
        :model="userProfile"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item
          label="Profile"
          class="formField"
        >
          <div v-if="userProfile.imageUrl" class="image-preview">
            <img class="preview" :src="userProfile.imageUrl">
          </div>
          <div v-else class="image-preview">
            <img class="preview" src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png">
          </div>
          <div class="file-upload-form">
            <label class="custom-file-upload">
              <input type="file" accept="image/*" @change="previewImage">
              Upload Profile
            </label>
          </div>
        </el-form-item>

        <!--        <el-form-item-->
        <!--          v-if="currentUser.registration_type !== 'Student'"-->
        <!--          label="Office"-->
        <!--          prop="office"-->
        <!--        >-->
        <!--          <el-input v-model="userProfile.office" class="formField" />-->
        <!--        </el-form-item>-->

        <el-form-item
          label="Full Name"
          prop="name"
        >
          <el-input v-model="userProfile.name" class="formField" />
        </el-form-item>

        <el-form-item
          v-if="currentUser.registration_type === 'Student'"
          label="College"
          prop="college"
        >
          <el-input v-model="userProfile.college" class="formField" />
        </el-form-item>

        <el-form-item
          v-if="currentUser.registration_type === 'Student'"
          label="Year"
          prop="year"
        >
          <el-input v-model="userProfile.year" class="formField" />
        </el-form-item>

        <el-form-item
          v-if="currentUser.registration_type === 'Student'"
          label="Section"
          prop="section"
        >
          <el-input v-model="userProfile.section" class="formField" />
        </el-form-item>

        <el-form-item
          label="Email"
          prop="email"
        >
          <el-input v-model="userProfile.email" class="formField" />
        </el-form-item>

        <el-form-item
          label="Password"
          prop="password"
        >
          <el-input v-model="userProfile.password" class="formField" type="password" />
        </el-form-item>

        <!--        <el-form-item-->
        <!--          v-if="currentUser.registration_type !== 'Student'"-->
        <!--          label="Referral Code"-->
        <!--          prop="referral"-->
        <!--        >-->
        <!--          <el-input v-model="userProfile.referral_code" class="formField" />-->
        <!--        </el-form-item>-->

        <el-form-item
          label="Contact No"
          prop="contact"
        >
          <el-input v-model="userProfile.mobile_number" class="formField" />
        </el-form-item>

        <el-form-item
          label="Address"
          prop="address"
        >
          <el-input v-model="userProfile.address" class="formField" />
        </el-form-item>

        <el-form-item>
          <el-button class="button-primary" @click.native.prevent="handleSubmit">Save</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { allUsers, createUpdateProfile } from '@/graphql/users'

export default {
  name: 'Profile',
  data() {
    const client = process.env.VUE_APP_CLIENT
    return {
      formLoading: true,
      client: client,
      currentUser: JSON.parse(localStorage.getItem('user')),
      userProfile: {
        id: undefined,
        user_type: '',
        referral_code: '',
        imageUrl: '',
        office: '',
        name: '',
        year: '',
        section: '',
        college: '',
        email: '',
        password: '',
        phone_number: '',
        mobile_number: '',
        address: ''
      }
    }
  },
  mounted() {
    this.viewData()
  },
  methods: {
    viewData() {
      this.formLoading = true
      this.userProfile.id = this.currentUser.id
      this.userProfile.email = this.currentUser.email
      const query = {
        first: 1,
        page: 1,
        student: this.currentUser.email,
        registration_type: this.currentUser.registration_type,
        client: this.client
      }
      allUsers(query, (response, success) => {
        if (success) {
          this.formLoading = false
          const data = response.allUsers.data
          if (data.length > 0) {
            data.forEach(item => {
              this.userProfile.name = item.name
              this.userProfile.email = item.email
              this.userProfile.id = item.id
              this.userProfile.user_type = item.registration_type
              this.userProfile.address = item.guidance_info.address ?? ''
              this.userProfile.year = item.guidance_info.year ?? ''
              this.userProfile.section = item.guidance_info.section ?? ''
              this.userProfile.college = item.guidance_info.college ?? ''
              this.userProfile.mobile_number = item.contact_info.mobile_number ?? ''
              this.userProfile.imageUrl = `${item.other_info.photo}`
              this.userProfile.office = item.guidance_info.office ?? ''
              this.userProfile.referral_code = item.guidance_info.referral_code ?? ''
              this.userProfile.college = item.guidance_info.college ?? ''
            })
          } else {
            this.formLoading = false
          }
        } else {
          this.formLoading = false
        }
      })
    },
    previewImage(event) {
      // Reference to the DOM input element
      var input = event.target
      // Ensure that you have a file before attempting to read it
      if (input.files && input.files[0]) {
        // create a new FileReader to read this image and convert to base64 format
        var reader = new FileReader()
        // Define a callback function to run, when FileReader finishes its job
        reader.onload = (e) => {
          // Note: arrow function used here, so that "this.imageData" refers to the imageData of Vue component
          // Read image as base64 and set to imageData
          this.userProfile.imageUrl = e.target.result
        }
        // Start the reader job - read file as a data url (base64 format)
        reader.readAsDataURL(input.files[0])
      }
    },
    handleSubmit() {
      this.formLoading = true
      const query = {
        client: this.client,
        user_type: this.currentUser.registration_type,
        data: this.userProfile
      }
      createUpdateProfile(query, (response, success) => {
        if (success) {
          this.$message({
            type: 'success',
            message: response.createUpdateProfile.message
          })
          this.resetModel()
        } else {
          this.$message({
            type: 'error',
            message: response.createUpdateProfile.message
          })
          this.resetModel()
        }
      })
    },
    resetModel() {
      this.viewData()
    }
  }
}
</script>

