import { apolloClientInstance } from '@/vue-apollo'
import CreateUpdateStudentGuidanceAppointmentInfos from './CreateUpdateStudentGuidanceAppointmentInfos.graphql'
import AllStudentGuidanceAppointmentInfos from './AllStudentGuidanceAppointmentInfos.graphql'
import DeleteGuidanceAppointment from './DeleteGuidanceAppointment.graphql'

export function allStudentGuidanceAppointmentInfos(query, callback) {
  if (apolloClientInstance) {
    apolloClientInstance.query({
      query: AllStudentGuidanceAppointmentInfos,
      variables: query
    }).then(function(response) {
      callback(response.data, true)
    }).catch(function(response) {
      callback(response, false)
    })
  }
}

export function createUpdateStudentGuidanceAppointmentInfos(query, callback) {
  if (apolloClientInstance) {
    apolloClientInstance.mutate({
      mutation: CreateUpdateStudentGuidanceAppointmentInfos,
      variables: query
    }).then(function(response) {
      callback(response.data, true)
    }).catch(function(response) {
      callback(response, false)
    })
  }
}

export function deleteGuidanceAppointment(query, callback) {
  if (apolloClientInstance) {
    apolloClientInstance.mutate({
      mutation: DeleteGuidanceAppointment,
      variables: query
    }).then(function(response) {
      callback(response.data, true)
    }).catch(function(response) {
      callback(response, false)
    })
  }
}
